
(function() {
  'use strict';

  angular
    .module('horseAdmin', ['ngResource', 'ui.router', 'ui.bootstrap', 'toastr'])

     //.constant('backendURL', 'http://football-back-dev.ap-southeast-1.elasticbeanstalk.com/admin')
  //http://racingprohk.ap-southeast-1.elasticbeanstalk.com/admin
  //.constant('backendURL', 'http://localhost/admin')
      .factory('AuthService',function(){
      var currentUser=null;
      var curentToken=null;
      var AuthService= {
          
          login:function(user,token){
              currentUser=user;
              curentToken=token;
             
          },
          logout:function(){
              currentUser=null;
             curentToken=null;

              
          },
          isLogined:function(){
              if(!curentToken)
              return false;
              return true;
          },
          getCurrentUser:function(){
          
          return currentUser;
            
            },
          getCurentToken:function(){
          
          return curentToken;
            
            }
          
          
      }
      
      return AuthService;
      
      
  })
        .factory('authInterceptor', function ($rootScope, $q, $window,AuthService) {
  return {
    request: function (config) {
      config.headers = config.headers || {};
      if (AuthService.getCurentToken()) {
        config.headers.token = AuthService.getCurentToken();
      }
      return config;
    }
  }
  
});;

})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('UpdateController', UpdateController);

  /** @ngInject */
  function UpdateController($http, $scope,$rootScope,toastr,AuthService,$location, $uibModalInstance,selected_user) {

  $scope.update={};
     $scope.update._id=selected_user._id;
      $scope.update.note=selected_user.note;
           $scope.update.salePrice=selected_user.salePrice;
     $scope.update.password=selected_user.password;      
      
      
 $scope.updateMember = function(user){

     if(user.password=="")return toastr.warning("Password cannot be empty","Warning");
     $http.put($rootScope.backendURL+'/api/users/'+user._id,user).then(function(res){
          $rootScope.users=res.data;
         toastr.success("Success");
         $uibModalInstance.close();
         $rootScope.setPage(1);
     },function(res){
         toastr.error(res.data,"Error");

     })
 }


$scope.closeModel = function(){

      $uibModalInstance.dismiss('cancel');
    }


  }
})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('RaceController', RaceController);

  /** @ngInject */
  function RaceController($http,$scope,$rootScope,toastr,AuthService) {

var backendURL='http://racingprohk.ap-southeast-1.elasticbeanstalk.com';//a seperate link
$scope.selectText='Select Race';
$scope.resetText='Reset';
$scope.saveText='Save';

function getRaces(){
    

      $http.get(backendURL+'/fetchRace').then(function(res){
          $scope.races=res.data;
      },function(err){

      })

  }

getRaces();




function selectRace(){
   
  $scope.selectText='Loading...';
   $scope.resetText='Reset';
         $http({
  method: 'POST',
  url: backendURL+'/',
 data:{link:$scope.selectedlink,lang:'chinese'}
}).then(function(res){
       
        $scope.selectedRace=res.data;
         var horseList=$scope.race.hl;
          if(horseList && horseList.length==0)
           {
               toastr.error('Failed to get any data!')
$scope.selectText='Select Race';

           
           return;
           }
          
$scope.selectText='Select Race';

      },function(err){ toastr.error('Failed!'+err)
$scope.selectText='Select Race';
})
    
        
    
    
    
    
     
}


 $scope.selectRace=selectRace;

      
$scope.saveRace=function(){
   
    

    console.log('saving race'+$scope.selectedRace.length)
    if(!$scope.selectedRace){
         toastr.error('No race to save!');
        $scope.saveText='Saving';
        return;
        
    }
    $scope.saveText='Saving';
     $http({
  method: 'POST',
  url: backendURL+'/saveRace',
 data:{race:$scope.selectedRace}
}).then(function(res){
       $scope.saveText='Save';
          console.log(res)
          $scope.selectedRace=res.data.race;
          toastr.success('Race has been updated ')
      },function(err){$scope.saveText='Save'; toastr.error('Failed!'+err)})

     
}      
      


$scope.resetRace=function(link){
    $scope.resetText='Resetting';
 
     $http({
  method: 'POST',
  url: backendURL+'/resetRace/',
data:{raceLink:$scope.selectedRace.raceLink}
}).then(function(res){
       
         selectRace();
          
      },function(err){ toastr.error('Failed!'+err)})
    
    
}

  }
})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/view/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($rootScope,$scope,AuthService,toastr,$location) {
        
         $scope.navText=$rootScope.navText;
        $scope.logout=function(){
            
            if(confirm("Sure to Log out?")){
            toastr.success("Log out Successfully!!")
            AuthService.logout();
                $rootScope.dp_background=false;
            $location.path("/login")
            }
        }

    }
  }

})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('MultipleController', MultipleController);

  /** @ngInject */
  function MultipleController($http, $rootScope,$scope,toastr) {
         
   
 
 
      function getMultipleLogin(){
         
          $http.get($rootScope.backendURL+'/api/multiplelogin').then(function(res){
             
                var lastUser=null;
              var multiples=[];
              var multipleCount = 0;
              for(var i=0;i<res.data.length;i++)
                  {
                      var user =res.data[i];
                      if(user.username!=lastUser)           
                      {
                          user.count = 1;
                          lastUser=user.username;
                          multiples.push(user);
                         
                          
                      }else
                          {
                             multiples[(multiples.length-1)].count++; 
                              multiples[(multiples.length-1)].date=user.date;
                              
                          }
                     
                     
                     
                        
                  }
              
              
              
              
              $scope.multiplelogin  = multiples;
          },function(res){
              toastr.error(res.data)
          })
      }
      
      
  
 
   
      
      getMultipleLogin();
    
  
 
 
 $scope.emptyRecords = function(){
     
     $http.delete($rootScope.backendURL+'/api/multiplelogin/').then(function(res){
         toastr.success(res.data,"Success");
         getMultipleLogin();
         console.log('empty!')
     },function(res){})
 }

  
  }
})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('MemberController', MemberController);

  /** @ngInject */
  function MemberController($http, $scope,$rootScope,toastr,$uibModal) {

    var updateModel,createModel;

      $scope.user={};
      $scope.showpassword=false;
        /** @pagination */

      
        $scope.currentPage = 1;
      $scope.itemsPerPage=100;

  $rootScope.setPage=function(pnum)
  {
       $scope.currentPage = pnum;
  }

  $scope.pageChanged = function() {
    $rootScope.getMembers(($scope.currentPage-1)*$scope.itemsPerPage);
  };

    if($rootScope.navText=='Dream Project'){
          
          $rootScope.dp_background=true;
      }
     
      
      
      $scope.showpass=function(){

           $scope.showpassword=!$scope.showpassword;

      }

     
      
      
      
     

      $rootScope.getMembers=function(prows){
      $http.get($rootScope.backendURL+'/api/users/'+prows+'/'+$scope.itemsPerPage).then(function(res){
          
         
          $rootScope.users=res.data.users;
         $scope.totalItems=res.data.total_count;

      })
     
     

  }
      $rootScope.searchMember=function(keyword){
        
          $http.get($rootScope.backendURL+'/api/users/'+keyword).then(function(res){
          
         
            $rootScope.users=res.data;
            $scope.totalItems=res.data;
            if(res.data.length>100)
                {
                    alert("We can only present 100 rows of result for you !")
                }

      })   
      }





        $rootScope.getMembers(0);




 $scope.deleteMember=function(id){
     if(confirm("Sure to delete?")){
     $http.delete($rootScope.backendURL+'/api/users/'+id).then(function(res){
         toastr.success("Success");
         console.log(res.data)
          $rootScope.users=res.data;
          $rootScope.setPage(1);
     },function(err){})
     }
 }

 
  $scope.showAddMember = function(id){

     $('#addModel').modal('show');
     $scope.addMember={};
      $scope.addMember.role="member";
     $scope.roles=[
          {label:"Member",value:"member"},
          {label:"Admin",value:"admin"}

      ]

     console.log($scope.roles);
 }


 $scope.showUpdateMember = function(user){

     $('#updateModel').modal('show');
     $scope.update={};
     $scope.update._id=user._id;
      $scope.update.note=user.note;
           $scope.update.salePrice=user.salePrice;
     $scope.update.password=user.password;

 }


 
  $scope.openUpdate = function (user) {

    $uibModal.open({
      animation: true,
      templateUrl: 'app/view/updateModel.html',
      controller: 'UpdateController',

      resolve: {
        selected_user: function () {
          return user;
        }
      }
    });
 }

  
   $scope.openCreate = function () {

   $uibModal.open({
      animation: true,
      templateUrl: 'app/view/createModel.html',
      controller: 'CreateController',

      resolve: {
        items: function () {
          return $scope.items;
        }
      }
    });
 }

 
  }
})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($http,$rootScope, $scope,toastr,AuthService,$location) {

 $scope.user={
     
 }

 
 
 
$scope.platform='Dream Project';
$rootScope.navText ='Admin Panel';
      
 $scope.adminLogin=function(user){

   
     
     if($scope.platform=='Racing Pro')
         {
        $rootScope.navText ='Racing Pro ';

         $rootScope.backendURL="http://racingprohk.ap-southeast-1.elasticbeanstalk.com/admin";    
         }
     else{
         
          $rootScope.backendURL="http://football-back-dev.ap-southeast-1.elasticbeanstalk.com/admin";
          $rootScope.navText ='Dream Project';
     }
    
     
     $http.post($rootScope.backendURL+'/api/login',{id:user.id,password:user.password}).then(function(res){
        toastr.success("Login Successfully!!")

        AuthService.login(user,res.data.token);
         $location.path("/")


      },function(err){
                    console.log(err);
         toastr.error("Auth Failed!")

      })



  }






  }
})();

