(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .controller('AccountController', AccountController);

  /** @ngInject */
  function AccountController($http, $scope,$rootScope,toastr,AuthService,$location) {

 
 
$scope.open1 = function() {
    $scope.popup1.opened = true;
  };

  $scope.open2 = function() {
    $scope.popup2.opened = true;
  };

  $scope.setDate = function(year, month, day) {
    $scope.dt = new Date(year, month, day);
  };

  $scope.dateOptions = {
    formatYear: 'yy',
    startingDay: 1
  };


  $scope.popup1 = {
    opened: false
  };

  $scope.popup2 = {
    opened: false
  };
      
      
      
 $scope.adminLogin=function(user){

     $http.post( $rootScope.backendURL+'/api/login',{id:user.id,password:user.password}).then(function(res){
        toastr.success("Login Successfully!!")

        AuthService.login(user,res.data.token);
         $location.path("/")


      },function(err){
                    console.log(err);
         toastr.error("Auth Failed!")

      })



  }

$scope.getRange=function(from,to){
    if(!from||!to)
        return toastr.error('Please select a date range first.')
        
    $http.get( $rootScope.backendURL+'/api/rangetotal/'+from+'/'+to).then(function(res){
        
$scope.rangetotal=res.data.totalcost;    
$scope.totalusers=res.data.users;


      },function(err){
                    console.log(err);
         toastr.error("Auth Failed!")

      })
    
    
    
}

 
function getTotal(){
    $http.get( $rootScope.backendURL+'/api/daytotal').then(function(res){
        
$scope.daytotal=res.data.daytotal;    
        


      },function(err){
                    console.log(err);
         toastr.error("Auth Failed!")

      })
    
    $http.get($rootScope.backendURL+'/api/monthtotal').then(function(res){
        
$scope.monthtotal=res.data.monthtotal;    
        


      },function(err){
                    console.log(err);
         toastr.error("Auth Failed!")

      })
    
}

getTotal();

  }
})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log,$rootScope,AuthService,$location) {

    $log.debug('runBlock end');



      $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {

          

            if(toState.authRequired){
                 if (!AuthService.isLogined()) {

                   return $location.path('/login')
                }

             }
            if (toState.templateUrl === 'app/view/login.html' && AuthService.isLogined()) {

                $location.path('/home')
            }




        });


  }

})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('current', {
        url: '/current',
        templateUrl: 'app/view/current.html',
        controller: 'CurrentController',
        controllerAs: 'current',
        authRequired:true
      })
    .state('login', {
        url: '/login',
        templateUrl: 'app/view/login.html',
        controller: 'LoginController',
        controllerAs: 'login',
        authRequired:false
      })
    .state('member', {
        url: '/',
        templateUrl: 'app/view/member.html',
        controller: 'MemberController',
        controllerAs: 'member',
        authRequired:true,
      })
     .state('multiple', {
        url: '/multiple',
        templateUrl: 'app/view/multiple.html',
        controller: 'MultipleController',
        controllerAs: 'multiple',
        authRequired:true
      })
        .state('account', {
        url: '/accounting',
        templateUrl: 'app/view/account.html',
        controller: 'AccountController',
        controllerAs: 'account',
        authRequired:true
      })
     .state('race', {
        url: '/race',
        templateUrl: 'app/view/race.html',
        controller: 'RaceController',
        controllerAs: 'race',
        authRequired:true
      })
    ;

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('horseAdmin')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig,$httpProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 2000;
    toastrConfig.positionClass = 'toast-top-right';
       $httpProvider.interceptors.push('authInterceptor');
  }

})();

